import Grid from "@material-ui/core/Grid";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import { getAdjacentKeyPoints, load as loadPoseNet, Pose, PoseNet } from '@tensorflow-models/posenet';
import { Vector2D } from "@tensorflow-models/posenet/dist/types";
import * as tfjs from '@tensorflow/tfjs';
import React, { useEffect, useRef, useState } from "react";
import { coco_skeleton_indices } from '../../config/skeleton';
import { spine } from '../../lib/spine/spine-webgl';
import PoseAPI from "../pose/PoseAPI";
import CanvasCard from "./CanvasCard";
import useCanvas from "./useCanvas";
import useWebcam from "./useWebcam";



type Model2D = {
    skeleton: spine.Skeleton,
    state: spine.AnimationState,
    bounds: { offset: number, size: number },
    premultipliedAlpha: boolean
}

type PoseSkeleton = {
    root: spine.webgl.Bone,
    hip: spine.webgl.Bone,
    neck: spine.webgl.Bone,
    head: spine.webgl.Bone,
    leftArm: spine.webgl.Bone,
    leftForeArm: spine.webgl.Bone,
    rightArm: spine.webgl.Bone,
    rightForeArm: spine.webgl.Bone,
    leftUpLeg: spine.webgl.Bone,
    leftLeg: spine.webgl.Bone,
    rightUpLeg: spine.webgl.Bone,
    rightLeg: spine.webgl.Bone
}

type PoseSkeletonKeypointName = {
    root: string,
    hip: string,
    neck: string,
    head: string,
    leftArm: string,
    leftForeArm: string,
    rightArm: string,
    rightForeArm: string,
    leftUpLeg: string,
    leftLeg: string,
    rightUpLeg: string,
    rightLeg: string
}

type Model2DInfo = {
    atlas: string,
    json: string,
    skinName: string,
    initialAnimation: string,
    poseSkeletonKeypointName: PoseSkeletonKeypointName,
    scale: number
}


function calculateSetupPoseBounds(skeleton) {
    skeleton.setToSetupPose();
    skeleton.updateWorldTransform();
    const offset = new spine.Vector2();
    const size = new spine.Vector2();
    skeleton.getBounds(offset, size, []);
    return { offset: offset, size: size };
}


// result angle is x-axis flipped angle. because esimated coords have y-axis inverted in graphical coord standard
function getAngle(initPt: { x: number; y: number; }, termPt: { x: number; y: number; }) {
    const x = termPt.x - initPt.x;
    const y = termPt.y - initPt.y;
    const l = Math.sqrt(x ** 2 + y ** 2);
    const co = x / l;
    let angle: number;

    if (y > 0) { angle = Math.acos(co) * (180 / Math.PI) } else { angle = 360 - Math.acos(co) * (180 / Math.PI) }

    //flip the angle upon x-axis, because coords has y-axis inverted.
    angle = 360 - angle;

    return angle;
}

function drawPoint(ctx, x, y, r, color) {
    ctx.beginPath();
    ctx.arc(x, y, r, 0, 2 * Math.PI);
    ctx.fillStyle = color;
    ctx.fill();
}

/**
 * Draw pose keypoints onto a canvas
 */
function drawKeypoints(keypoints, minConfidence, ctx, scale = 1, color) {
    for (let i = 0; i < keypoints.length; i++) {
        const keypoint = keypoints[i];

        if (keypoint.score < minConfidence) {
            continue;
        }

        const { x, y } = keypoint.position;
        drawPoint(ctx, x * scale, y * scale, 3, color);
    }
}
function toTuple(data: Vector2D): [number, number] {
    return [data.x, data.y];
}
/**
 * Draws a pose skeleton by looking up all adjacent keypoints/joints
 */
function drawSkeleton(keypoints, minConfidence, ctx, scale = 1, color) {
    const adjacentKeyPoints =
        getAdjacentKeyPoints(keypoints, minConfidence);

    adjacentKeyPoints.forEach((keypoints) => {
        drawSegment(
            toTuple(keypoints[0].position),
            toTuple(keypoints[1].position),
            color,
            scale, ctx);
    });
}

/**
 * Draws a line on a canvas, i.e. a joint
 */
function drawSegment([ax, ay]: [number, number], [bx, by]: [number, number], color, scale, ctx) {
    ctx.beginPath();
    ctx.moveTo(ax * scale, ay * scale);
    ctx.lineTo(bx * scale, by * scale);
    ctx.lineWidth = 2;
    ctx.strokeStyle = color;
    ctx.stroke();
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            flexGrow: 1,
        }
    }),
);


export function PoseAnimation() {
    const [net, setNet] = useState<PoseNet>(null)
    const [assetManager, setAssetManager] = useState<spine.webgl.AssetManager>(null)
    const [initialized, setInitialized] = useState<boolean>(false)
    const [managerLoaded, setManagerLoaded] = useState<boolean>(false)
    const [videoLoadedData, setVideoLoadedData] = useState<boolean>(false)
    const [model2D, setModel2D] = useState<Model2D>(null);
    const [bonesToInvisible, setBonesToInvisible] = useState<Array<string>>([])
    const [skeletonRender, setSkeletonRender] = useState<spine.webgl.SceneRenderer>(null)
    const [poseSkeleton, setPoseSkeleton] = useState<PoseSkeleton>();

    const skeletonKeypointName = {
        hip: "hip", rightUpLeg: "front-thigh", rightLeg: "front-shin",
        leftUpLeg: "rear-thigh", leftLeg: "rear-shin", neck: "neck",
        head: "head", rightArm: "front-upper-arm", rightForeArm: "front-bracer",
        leftArm: "rear-upper-arm", leftForeArm: "rear-bracer", root: "root"
    }
    const [model2DInfo, setModel2DInfo] = useState<Model2DInfo>(
        {
            atlas: "assets/spineboy.atlas",
            json: "assets/spineboy-ess.json",
            skinName: "default",
            initialAnimation: "idle",
            poseSkeletonKeypointName: skeletonKeypointName,
            scale: 0.5
        }
    )

    const [humanPose, setHumanPose] = useState<Pose>(null)
    const [animationPose, setAnimationPose] = useState<Pose>(null)
    const [animationPoseRendered, setAnimationPoseRendered] = useState<boolean>(true)

    const humanWidth = 400
    const humanHeight = 300
    const animationWidth = 400
    const animationHeight = 300
    const webcamRef = useWebcam(humanWidth, humanHeight);
    const minConfidence = 0.6
    const faceMaskImageRef = useRef<HTMLImageElement>(null)

    useEffect(() => {
        const image = faceMaskImageRef.current
        if (faceMaskImageRef.current) {
            image.src = 'shasha.jpeg'
        }
    }, [faceMaskImageRef.current])

    useEffect(() => {
        if (webcamRef.current) {
            webcamRef.current.onloadeddata = (event) => {
                setVideoLoadedData(true)
            };
        }
    }, [webcamRef.current])

    useEffect(() => {
        const getPose = async () => {
            const pose = await net.estimateSinglePose(webcamRef.current, {
                flipHorizontal: false
            });
            // const pose = (await PoseAPI.getPose())[0]

            setAnimationPose(pose)
            setHumanPose(pose)
            setAnimationPoseRendered(false)
        }
        initialized && animationPoseRendered && getPose()
    }, [initialized, animationPoseRendered])

    const loadModel2D = (
        premultipliedAlpha: boolean
    ): Model2D => {
        // Load the texture atlas from the AssetManager.

        const atlas = assetManager.get(model2DInfo.atlas);

        // Create a AtlasAttachmentLoader that resolves region, mesh, boundingbox and path attachments
        const atlasLoader = new spine.AtlasAttachmentLoader(atlas);

        // Create a SkeletonBinary instance for parsing the .skel file.
        const skeletonJson = new spine.SkeletonJson(atlasLoader);

        // Set the scale to apply during parsing, parse the file, and create a new skeleton.
        skeletonJson.scale = model2DInfo.scale;
        const skeletonData = skeletonJson.readSkeletonData(assetManager.get(model2DInfo.json));
        const skeleton = new spine.Skeleton(skeletonData);
        skeleton.setSkinByName(model2DInfo.skinName);
        const bounds = calculateSetupPoseBounds(skeleton);

        // Create an AnimationState, and set the initial animation in looping mode.
        const animationStateData = new spine.AnimationStateData(skeleton.data);
        const animationState = new spine.AnimationState(animationStateData);
        animationState.setAnimation(0, model2DInfo.initialAnimation, true);

        // Pack everything up and return to caller.
        return { skeleton: skeleton, state: animationState, bounds: bounds, premultipliedAlpha: premultipliedAlpha };
    }

    const drawHuman = (context: RenderingContext) => {
        if (initialized && context instanceof CanvasRenderingContext2D && humanPose) {
            //draw webcam on human canvas
            context.save();
            context.drawImage(webcamRef.current, 0, 0, humanWidth, humanHeight);
            //un-comment line below if you want to hide your face for record n share purpose
            context.drawImage(faceMaskImageRef.current, humanPose.keypoints[coco_skeleton_indices.nose].position.x - 75, humanPose.keypoints[coco_skeleton_indices.nose].position.y - 63, 150, 126);
            context.restore();
            drawKeypoints(humanPose.keypoints, minConfidence, context, 1, "#00ff00");
            drawSkeleton(humanPose.keypoints, minConfidence, context, 1, "#ffffff");
        }
    }


    const drawAnimation = (context: RenderingContext) => {
        // Get two keypoints, a character bone and minConfidence
        // Animate bone if minimum confidence condition is met
        const animateDetectedBone = (chaBone, initKeyPt, termKeyPt, minConfidence, skeleton) => {

            if (model2DInfo.poseSkeletonKeypointName.hip == chaBone.data.name) {
                chaBone.x = animationWidth / 2
                chaBone.y = animationHeight / 2
            }
            if (initKeyPt.score > minConfidence && termKeyPt.score > minConfidence) {
                chaBone.rotation = chaBone.worldToLocalRotation(getAngle(initKeyPt.position, termKeyPt.position));

                if ([
                    model2DInfo.poseSkeletonKeypointName.head,
                    // model2DInfo.poseSkeletonKeypointName.hip,
                    model2DInfo.poseSkeletonKeypointName.neck
                ].includes(chaBone.data.name)) {
                    if (initKeyPt.position.x < termKeyPt.position.x) {
                        chaBone.rotation += 90;
                    }
                    else {
                        chaBone.rotation -= 90;
                    }
                }
                // skeleton's all bones should be updated whenever a bone's transform is changed.
                skeleton.updateWorldTransform();
            }

        }

        if (initialized && context instanceof WebGLRenderingContext && !animationPoseRendered && animationPose) {
            const pose = animationPose

            context.clearColor(0.3, 0.3, 0.3, 1);
            context.clear(context.COLOR_BUFFER_BIT);

            //prepare for skeleton data to animate upon coords of estimated pose
            const skeleton = model2D.skeleton;

            skeletonRender.begin();

            // manipulate bones manually upon pose detection
            animateDetectedBone(poseSkeleton.head, pose.keypoints[coco_skeleton_indices.rightEye], pose.keypoints[coco_skeleton_indices.leftEye], minConfidence, skeleton);
            animateDetectedBone(poseSkeleton.leftUpLeg, pose.keypoints[coco_skeleton_indices.leftHip], pose.keypoints[coco_skeleton_indices.leftKnee], minConfidence, skeleton);
            animateDetectedBone(poseSkeleton.leftLeg, pose.keypoints[coco_skeleton_indices.leftKnee], pose.keypoints[coco_skeleton_indices.leftAnkle], minConfidence, skeleton);
            animateDetectedBone(poseSkeleton.rightUpLeg, pose.keypoints[coco_skeleton_indices.rightHip], pose.keypoints[coco_skeleton_indices.rightKnee], minConfidence, skeleton);
            animateDetectedBone(poseSkeleton.rightLeg, pose.keypoints[coco_skeleton_indices.rightKnee], pose.keypoints[coco_skeleton_indices.rightAnkle], minConfidence, skeleton);
            animateDetectedBone(poseSkeleton.leftArm, pose.keypoints[coco_skeleton_indices.leftShoulder], pose.keypoints[coco_skeleton_indices.leftElbow], minConfidence, skeleton);
            animateDetectedBone(poseSkeleton.leftForeArm, pose.keypoints[coco_skeleton_indices.leftElbow], pose.keypoints[coco_skeleton_indices.leftWrist], minConfidence, skeleton);
            animateDetectedBone(poseSkeleton.rightArm, pose.keypoints[coco_skeleton_indices.rightShoulder], pose.keypoints[coco_skeleton_indices.rightElbow], minConfidence, skeleton);
            animateDetectedBone(poseSkeleton.rightForeArm, pose.keypoints[coco_skeleton_indices.rightElbow], pose.keypoints[coco_skeleton_indices.rightWrist], minConfidence, skeleton);
            animateDetectedBone(poseSkeleton.hip, pose.keypoints[coco_skeleton_indices.rightHip], pose.keypoints[coco_skeleton_indices.leftHip], minConfidence, skeleton);
            animateDetectedBone(poseSkeleton.neck, pose.keypoints[coco_skeleton_indices.rightShoulder], pose.keypoints[coco_skeleton_indices.leftShoulder], minConfidence, skeleton);

            skeleton.updateWorldTransform();



            //draw on animal canvas
            skeletonRender.drawSkeleton(skeleton, false);

            skeletonRender.drawSkeletonDebug(skeleton, false, bonesToInvisible);


            skeletonRender.end();
            setAnimationPoseRendered(true)
        }
    }

    const initManager = (context: WebGLRenderingContext) => {
        //create asset manager object
        const aManager = new spine.webgl.AssetManager(context)


        //create rendering object
        setSkeletonRender(new spine.webgl.SceneRenderer(context.canvas, context));

        aManager.loadTextureAtlas(model2DInfo.atlas);
        aManager.loadText(model2DInfo.json);
        setAssetManager(aManager)
    }

    const loadManager = () => {
        if (assetManager.isLoadingComplete()) {
            setManagerLoaded(true)
        } else {
            setTimeout(loadManager, 1000)
        }
    }

    const initSkeleton = (canvasWidth: number, canvasHeight: number) => {
        //parachute animation preset is good for motion capture, because it disabled all IK bones and transform constrains.
        const model2D = loadModel2D(false)

        // Assign every targeted bones. we do this before rendering, else searching for this bone will need searching through 30-long lists each time, each frame.
        const curPoseSkeleton = {
            root: model2D.skeleton.findBone(model2DInfo.poseSkeletonKeypointName.root),
            hip: model2D.skeleton.findBone(model2DInfo.poseSkeletonKeypointName.hip),
            neck: model2D.skeleton.findBone(model2DInfo.poseSkeletonKeypointName.neck),
            head: model2D.skeleton.findBone(model2DInfo.poseSkeletonKeypointName.head),
            leftUpLeg: model2D.skeleton.findBone(model2DInfo.poseSkeletonKeypointName.leftUpLeg),
            leftLeg: model2D.skeleton.findBone(model2DInfo.poseSkeletonKeypointName.leftLeg),
            rightUpLeg: model2D.skeleton.findBone(model2DInfo.poseSkeletonKeypointName.rightUpLeg),
            rightLeg: model2D.skeleton.findBone(model2DInfo.poseSkeletonKeypointName.rightLeg),
            leftArm: model2D.skeleton.findBone(model2DInfo.poseSkeletonKeypointName.leftArm),
            leftForeArm: model2D.skeleton.findBone(model2DInfo.poseSkeletonKeypointName.leftForeArm),
            rightArm: model2D.skeleton.findBone(model2DInfo.poseSkeletonKeypointName.rightArm),
            rightForeArm: model2D.skeleton.findBone(model2DInfo.poseSkeletonKeypointName.rightForeArm),
        }
        setPoseSkeleton(curPoseSkeleton);

        // initial update
        model2D.state.update(0.0333);
        model2D.state.apply(model2D.skeleton);

        //camera(in Spine runtime) position's pivot point is middle point, therefore we devide dimentions by 2 and set the camera position to there so that the bottom left point of viewport camera becomes 0, 0
        skeletonRender.camera.position.set(canvasWidth / 2, canvasHeight / 2, 0);

        //options of debugging 
        skeletonRender.skeletonDebugRenderer.drawBones = true;
        skeletonRender.skeletonDebugRenderer.drawRegionAttachments = false;
        skeletonRender.skeletonDebugRenderer.drawBoundingBoxes = false;
        skeletonRender.skeletonDebugRenderer.drawMeshHull = false;
        skeletonRender.skeletonDebugRenderer.drawMeshTriangles = false;
        skeletonRender.skeletonDebugRenderer.drawPaths = false;
        skeletonRender.skeletonDebugRenderer.drawSkeletonXY = false;
        skeletonRender.skeletonDebugRenderer.drawClipping = false;
        skeletonRender.skeletonDebugRenderer.premultipliedAlpha = true;

        const invisibleBones = []
        // we make list for bones to hide in debug
        const validBones = Object.values(model2DInfo.poseSkeletonKeypointName)
        for (let i = 0, n = model2D.skeleton.bones.length; i < n; i++) {
            const _bone = model2D.skeleton.bones[i];
            if (!validBones.includes(_bone.data.name)) {
                invisibleBones.push(_bone.data.name)
            }
        }
        setBonesToInvisible(invisibleBones)
        setModel2D(model2D);
    }


    const animationContextType = "webgl"
    const humanContextType = "2d"
    const animationCanvasRef = useCanvas(drawAnimation, { context: animationContextType, width: animationWidth, height: animationHeight })
    const humanCanvasRef = useCanvas(drawHuman, { context: humanContextType, width: humanWidth, height: humanHeight })

    useEffect(() => {
        const loadModel = async () => {
            tfjs.setBackend('webgl')
            setNet(
                await loadPoseNet({
                    architecture: 'MobileNetV1',
                    outputStride: 16,
                    inputResolution: 500,
                    multiplier: 0.75,
                    quantBytes: 2
                })
            )
        }
        loadModel()
    }, []);
    useEffect(() => {
        animationCanvasRef.current && initManager(animationCanvasRef.current.getContext(animationContextType))
    }, [animationCanvasRef.current])
    useEffect(() => {
        assetManager && loadManager()
    }, [assetManager])
    useEffect(() => {
        managerLoaded && initSkeleton(animationWidth, animationHeight)
    }, [managerLoaded])
    useEffect(() => {
        managerLoaded && net && videoLoadedData && setInitialized(true)
    }, [managerLoaded && net && videoLoadedData])


    const classes = useStyles();

    return (
        <div className={classes.root}>
            {managerLoaded ? <h2>2D</h2> : <h2>2D...</h2>}
            {net ? <h2></h2> : <h2>...</h2>}
            {videoLoadedData ? <h2></h2> : <h2>...</h2>}
            <img ref={faceMaskImageRef} style={{ display: 'none' }}/>
            <video ref={webcamRef} playsInline={true} style={{ display: 'none' }} />

            <Grid
                container
                spacing={3}
                direction="column"
                justify="center"
                alignItems="stretch"
            >
                <Grid item xs={12}>
                    <CanvasCard canvasRef={humanCanvasRef} title={""} />
                </Grid>
                <Grid item xs={12}>
                    <CanvasCard canvasRef={animationCanvasRef} title={""} />
                </Grid>
            </Grid>
            {/* <canvas ref={humanCanvasRef} /> */}
            {/* <canvas ref={animationCanvasRef} /> */}
        </div>
    )
}
